// _header.scss

.header {
    background: $grey53;
    width: 100%;
    max-width: $contentWidth;
    padding: 0 2rem;
    margin: 0 auto;
    height: 9rem;
    display: flex;
    align-items: center;
    position: relative;
    z-index: 4;

    img {
        position: relative;
        z-index: 2;
        width: 7rem;
    }

    &:before {
        content: '';
        position: absolute;
        top: 0;
        left: -50vw;
        width: 200vw;
        height: 100%;
        background: $grey53;
        z-index: 0;
    }

    @media only screen and (max-width: $mobile) {
        height: 5.4rem;

        img {
            width: 4.9rem;
        }
    }
}
